"use client";

import { useEffect, useState } from "react";
import { createClient } from "@/lib/supabase/client";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Gamepad2, Wallet, ExternalLink, Copy, Trophy, Coins, TrendingUp, Users } from "lucide-react";
import Link from "next/link";
import { useRouter } from "next/navigation";
import { ethers } from "ethers";
import MetaMaskSDK from "@metamask/sdk";

interface User {
  id: string; 
  name: string;
  email: string;
  walletAddress: string;
  connectedWallet: string;
}

export default function Dashboard() {
  const [user, setUser] = useState<User | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [isConnectingWallet, setIsConnectingWallet] = useState(false);
  const router = useRouter();
  const supabase = createClient();





  useEffect(() => {
    const checkAuth = async () => {
      const { data: { session } } = await supabase.auth.getSession();
        console.log(session);
      if (!session) {
        router.push("/auth/login");
        return;
      }

      // Fetch user data from database
      try {
        const email =  session.user.email;
        console.log(email);
        const response = await fetch(`/api/user/${email}`);
        if (response.ok) {
          const userData = await response.json();
          setUser(userData);
        }
      } catch (error) {
        console.error("Error fetching user data:", error);
      } finally {
        setIsLoading(false);
      }
    };

    checkAuth();
  }, [router, supabase.auth]);

  const handleConnectWallet = async () => {
    setIsConnectingWallet(true);
    try {
          const MMSDK = new MetaMaskSDK({
            dappMetadata: {
              name: "Next.js Dapp",
              url: typeof window !== "undefined" ? window.location.href : "",
            },
            infuraAPIKey: "f302b612a16e4208b8f64973e42e3b84",
          });
    
          const ethereum = MMSDK.getProvider();
          console.log(ethereum);
          const accounts = await MMSDK.connect();
          console.log(accounts);
          console.log(accounts[0]);
          setUser({
            ...user,
            connectedWallet: accounts[0]
          });
        } catch (err) {
          console.error("MetaMask connection failed", err);
        }
        setIsConnectingWallet(false);
  };


  const handleCheckBalance = async ()=> {
    
  }

  const handleLogout = async () => {
    await supabase.auth.signOut();
    router.push("/");
  };

  const copyToClipboard = (text: string) => {
    navigator.clipboard.writeText(text);
  };

  if (isLoading) {
    return (
      <div className="min-h-screen arena-gradient flex items-center justify-center">
        <div className="text-center">
          <div className="w-16 h-16 bg-primary rounded-lg flex items-center justify-center arena-glow mx-auto mb-4">
            <Gamepad2 className="w-8 h-8 text-primary-foreground animate-pulse" />
          </div>
          <p className="text-muted-foreground">Loading your arena...</p>
        </div>
      </div>
    );
  }

  if (!user) {
    return (
      <div className="min-h-screen arena-gradient flex items-center justify-center">
        <div className="text-center">
          <p className="text-muted-foreground mb-4 animate-in duration-500">Welcome to ArenaFi</p>
          <Button onClick={() => router.push("/auth/create-wallet")}>Create a New Wallet</Button>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen arena-gradient">
      {/* Navigation */}
      <nav className="border-b border-border/40 backdrop-blur-sm bg-background/80">
        <div className="container mx-auto px-4 py-4 flex items-center justify-between">
          <Link href="/" className="flex items-center space-x-2">
            <div className="w-8 h-8 bg-primary rounded-lg flex items-center justify-center arena-glow">
              <Gamepad2 className="w-5 h-5 text-primary-foreground" />
            </div>
            <span className="text-2xl font-bold arena-text-glow">ArenaFi</span>
          </Link>
          <div className="flex items-center space-x-4">
            <span className="text-sm text-muted-foreground">Welcome, {user.name}</span>
            <Button variant="outline" onClick={handleLogout}>
              Logout
            </Button>
          </div>
        </div>
      </nav>

      <div className="container mx-auto px-4 py-8">
        {/* Welcome Section */}
        <div className="mb-8">
          <h1 className="text-4xl font-bold mb-2 arena-text-glow">
            Welcome to the Arena, {user.name}!
          </h1>
          <p className="text-xl text-muted-foreground">
            Your DeFi gaming dashboard is ready. Connect your external wallet to start battling.
          </p>
        </div>

        <div className="grid lg:grid-cols-3 gap-8">
          {/* Wallet Section */}
          <div className="lg:col-span-2 space-y-6">
            {/* Native Wallet */}
            <Card className="arena-border-glow">
              <CardHeader>
                <CardTitle className="flex items-center space-x-2">
                  <Wallet className="w-5 h-5 text-primary" />
                  <span>Native ArenaFi Wallet</span>
                  <Badge variant="secondary" className="arena-glow">Active</Badge>
                </CardTitle>
                <CardDescription>
                  Your secure, built-in wallet created during signup
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  <div>
                    <label className="text-sm font-medium text-muted-foreground">Wallet Address</label>
                    <div className="flex items-center space-x-2 mt-1">
                      <code className="flex-1 p-2 bg-muted rounded text-sm font-mono">
                        {user.walletAddress}
                      </code>
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={() => copyToClipboard(user.walletAddress)}
                      >
                        <Copy className="w-4 h-4" />
                      </Button>
                    </div>
                  </div>
                  <div className="grid grid-cols-2 gap-4">
                    <div className="text-center p-4 bg-muted/50 rounded-lg">
                      <div className="text-2xl font-bold text-primary">0.00</div>
                      <div className="text-sm text-muted-foreground">ETH Balance</div>
                    </div>
                    <div className="text-center p-4 bg-muted/50 rounded-lg">
                      <div className="text-2xl font-bold text-primary">0</div>
                      <div className="text-sm text-muted-foreground">$ARENA Tokens</div>
                    </div>
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* External Wallet Connection */}
            <Card className="arena-border-glow">
              <CardHeader>
                <CardTitle className="flex items-center space-x-2">
                  <ExternalLink className="w-5 h-5 text-primary" />
                  <span>External Wallet</span>
                  {user.connectedWallet && (
                    <Badge variant="secondary" className="arena-glow">Connected</Badge>
                  )}
                </CardTitle>
                <CardDescription>
                  Connect your MetaMask, WalletConnect, or other Web3 wallet
                </CardDescription>
              </CardHeader>
              <CardContent>
                {user.connectedWallet ? (
                  <div className="space-y-4">
                    <div>
                      <label className="text-sm font-medium text-muted-foreground">Connected Wallet</label>
                      <div className="flex items-center space-x-2 mt-1">
                        <code className="flex-1 p-2 bg-muted rounded text-sm font-mono">
                          {user.connectedWallet}
                        </code>
                        <Button
                          size="sm"
                          variant="outline"
                          onClick={() => copyToClipboard(user.connectedWallet)}
                        >
                          <Copy className="w-4 h-4" />
                        </Button>
                      </div>
                    </div>
                    <Button variant="outline" className="w-full">
                      Disconnect Wallet
                    </Button>
                  </div>
                ) : (
                  <div className="text-center py-8">
                    <div className="w-16 h-16 bg-muted/50 rounded-lg flex items-center justify-center mx-auto mb-4">
                      <Wallet className="w-8 h-8 text-muted-foreground" />
                    </div>
                    <p className="text-muted-foreground mb-4">
                      No external wallet connected
                    </p>
                    <Button 
                      onClick={handleConnectWallet} 
                      disabled={isConnectingWallet}
                      className="arena-glow"
                    >
                      {isConnectingWallet ? "Connecting..." : "Connect Wallet"}
                    </Button>
                  </div>
                )}
              </CardContent>
            </Card>
          </div>

          {/* Stats & Actions */}
          <div className="space-y-6">
            {/* Battle Stats */}
            <Card className="arena-border-glow">
              <CardHeader>
                <CardTitle className="flex items-center space-x-2">
                  <Trophy className="w-5 h-5 text-primary" />
                  <span>Battle Stats</span>
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="flex justify-between items-center">
                  <span className="text-muted-foreground">Battles Won</span>
                  <span className="font-bold">0</span>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-muted-foreground">Total Staked</span>
                  <span className="font-bold">0 ETH</span>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-muted-foreground">Rewards Earned</span>
                  <span className="font-bold text-primary">0 $ARENA</span>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-muted-foreground">Arena Rank</span>
                  <span className="font-bold">#--</span>
                </div>
              </CardContent>
            </Card>

            {/* Quick Actions */}
            <Card className="arena-border-glow">
              <CardHeader>
                <CardTitle>Quick Actions</CardTitle>
              </CardHeader>
              <CardContent className="space-y-3">
                <Button className="w-full arena-glow" disabled={!user.connectedWallet}>
                  <Coins className="w-4 h-4 mr-2" />
                  Start Battle
                </Button>
                <Button variant="outline" className="w-full arena-border-glow">
                  <TrendingUp className="w-4 h-4 mr-2" />
                  View Leaderboard
                </Button>
                <Button variant="outline" className="w-full arena-border-glow">
                  <Users className="w-4 h-4 mr-2" />
                  Join Community
                </Button>
              </CardContent>
            </Card>

            {/* Getting Started */}
            <Card className="arena-border-glow">
              <CardHeader>
                <CardTitle>Getting Started</CardTitle>
              </CardHeader>
              <CardContent className="space-y-3 text-sm">
                <div className="flex items-start space-x-2">
                  <div className="w-2 h-2 bg-primary rounded-full mt-2"></div>
                  <div>
                    <div className="font-medium">Connect External Wallet</div>
                    <div className="text-muted-foreground">Link your MetaMask or other Web3 wallet</div>
                  </div>
                </div>
                <div className="flex items-start space-x-2">
                  <div className="w-2 h-2 bg-muted-foreground rounded-full mt-2"></div>
                  <div>
                    <div className="font-medium">Fund Your Wallet</div>
                    <div className="text-muted-foreground">Add ETH to start battling</div>
                  </div>
                </div>
                <div className="flex items-start space-x-2">
                  <div className="w-2 h-2 bg-muted-foreground rounded-full mt-2"></div>
                  <div>
                    <div className="font-medium">Enter Your First Battle</div>
                    <div className="text-muted-foreground">Stake ETH and challenge opponents</div>
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
    </div>
  );
}
